.projects{
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    .projects-texts{
        width: 65%;

        .projects-title{
            font-size: 50px;
            font-weight: 600;
        }

        .projects-desc{
            margin: 20px 0px;
        }
    }

    .projects-list{
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        -webkit-flex-wrap: wrap;
        justify-content: center;
    }
    
}

@media screen and (max-width: 480px) {
    .projects{
        padding: 10px;

        .projects-texts{
            width: 100%
        }

        .projects-desc{
            display: none;
        }
    }
}