.toogle{
    width: 50px;
    height: 25px;
    border-radius: 20px;
    border: 1px solid #999;
    background-color: white;
    position: sticky;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: space-around;

    .toogle-icon{
        width: 15px;
        height: 15px;
    }

    .toogle-button{
        width: 25px;
        height: 25px;
        border-radius: 50%;
        background-color: #999;
        position: absolute;
        cursor: pointer;
    }
}